import OpenAI from "openai";

// DeepSeek client (OpenAI-compatible)
const client = new OpenAI({
  apiKey: process.env.DEEPSEEK_API_KEY,
  baseURL: "https://api.deepseek.com/v1",
});

/**
 * Decide whether AI is needed (COST CONTROL)
 */
function shouldCallAI(text) {
  if (!text) return false;
  if (text.length < 30) return false;
  if (!/â‚¹|\bRs\b|\bINR\b/i.test(text)) return false;
  return true;
}

/**
 * Safely extract + repair JSON from LLM output
 */
function parseLLMJson(raw) {
  if (!raw) throw new Error("Empty AI response");

  // Remove markdown fences
  let text = raw
    .trim()
    .replace(/^```json/i, "")
    .replace(/^```/, "")
    .replace(/```$/, "")
    .trim();

  // Extract JSON block only
  const start = text.indexOf("{");
  const end = text.lastIndexOf("}");
  if (start === -1 || end === -1) {
    throw new Error("No JSON object found");
  }

  text = text.slice(start, end + 1);

  // Repair common JSON issues
  text = text
    .replace(/,\s*}/g, "}")
    .replace(/,\s*]/g, "]")
    .replace(/\n/g, " ")
    .replace(/\t/g, " ");

  return JSON.parse(text);
}

/**
 * MAIN FUNCTION
 * Extracts payment info from OCR text
 */
export async function parsePaymentOCR(ocrText) {
  // ðŸ›‘ COST CONTROL: Skip AI if clearly useless
  if (!shouldCallAI(ocrText)) {
    return {
      amount: null,
      upiId: null,
      utr: null,
      appDetected: "UNKNOWN",
      isLikelyFake: false,
      confidence: 0,
      skippedAI: true,
    };
  }

  try {
    const prompt = `
Extract payment info from text.
Return ONLY raw JSON.

Rules:
- amount must be number or null
- upiId must contain "@"
- utr must be alphanumeric (8â€“25 chars) or null
- app must be one of: GPay, PhonePe, Paytm, AmazonPay, UNKNOWN
- fake = true if text looks edited, partial, or unclear
- confidence = number between 0 and 1

Schema:
{
  "amount": number | null,
  "upiId": string | null,
  "utr": string | null,
  "app": "GPay|PhonePe|Paytm|AmazonPay|UNKNOWN",
  "fake": boolean,
  "confidence": number
}

TEXT:
${ocrText}
`;

    const res = await client.chat.completions.create({
      model: "deepseek-chat",
      messages: [
        {
          role: "system",
          content:
            "You are a strict JSON generator. Output ONLY valid JSON. No markdown. No explanation.",
        },
        { role: "user", content: prompt },
      ],
      temperature: 0,
      max_tokens: 80,
    });

    const raw = res.choices[0].message.content;
    const parsed = parseLLMJson(raw);

    // âœ… FINAL NORMALIZATION
    return {
      amount: typeof parsed.amount === "number" ? parsed.amount : null,
      upiId:
        typeof parsed.upiId === "string" && parsed.upiId.includes("@")
          ? parsed.upiId
          : null,
      utr: typeof parsed.utr === "string" ? parsed.utr : null,
      appDetected: parsed.app || "UNKNOWN",
      isLikelyFake: Boolean(parsed.fake),
      confidence: Math.min(Math.max(parsed.confidence || 0.5, 0), 1),
    };
  } catch (err) {
    console.error("DeepSeek Parser ERROR â†’", err);

    // â— Safe fallback â€” never break API
    return {
      amount: null,
      upiId: null,
      utr: null,
      appDetected: "UNKNOWN",
      isLikelyFake: false,
      confidence: 0,
      aiError: true,
    };
  }
}



