"use client";

import { useState, useEffect } from "react";
import InvalidQRUI from "./invalidQRUI";
import ClaimCard from "./claimCard";
import { toast } from "sonner";
import { Sparkles } from "lucide-react";

const ClaimPage = ({ shopId }) => {
  const [isInvalid, setIsInvalid] = useState(false);
  const [shopName, setShopName] = useState("");
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);

  if (!shopId) return <InvalidQRUI />;

  useEffect(() => {
    const checkShop = async () => {
      try {
        const res = await fetch("/api/public/checkShop", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ shopId }),
        });

        const data = await res.json();

        if (!data.exists) {
          setIsInvalid(true);
        } else {
          setShopName(data.shop.shopName || "");
        }
      } catch (error) {
        console.error(error);
        toast.error("Invalid shop");
        setIsInvalid(true);
      }
    };

    checkShop();
  }, [shopId]);

  if (isInvalid) return <InvalidQRUI />;

  /* ------------------------------
     ðŸ”µ VERIFY SCREENSHOT
     (DeepSeek OCR on server)
  --------------------------------*/
  const verifyScreenshot = async (file, phone) => {
    if (!file) return;

    if (!phone || phone.length < 10) {
      toast.error("Please enter a valid phone number");
      return;
    }

    setLoading(true);
    setResult(null);

    try {
      const form = new FormData();
      form.append("file", file);
      form.append("shopId", shopId);
      form.append("phone", phone);

      const res = await fetch("/api/screenshot/verify", {
        method: "POST",
        body: form,
      });

      const data = await res.json();
      setLoading(false);

      if (data.success) {
        setResult({ success: true });
        toast.success("Payment Verified! Stamp added ðŸŽ‰");
      } else {
        const msg = data.error || data.rejectReason || "Verification failed";
        setResult({ error: msg });
        toast.error(msg);
      }

      console.log("API response:", data);
    } catch (err) {
      console.error("Verification Error:", err);
      setLoading(false);
      setResult({ error: "Something went wrong" });
      toast.error("Something went wrong");
    }
  };

  return (
    <div className="
      relative flex flex-col justify-between items-center
      min-h-screen p-8 text-center bg-custom-gradient gap-10
    ">
      {/* ---------- TOP ---------- */}
      <div className="mt-6 animate-fadeIn">
        <h2 className="text-2xl font-extrabold text-primary tracking-wide">
          {shopName || "Shop"}
        </h2>

        <div className="flex items-center justify-center gap-1 mt-1">
          <Sparkles className="w-4 h-4 text-primary/70" />
          <p className="text-sm text-primary/70 font-medium">
            You're one step away from rewards
          </p>
        </div>
      </div>

      {/* ---------- CARD ---------- */}
      <ClaimCard
        verify={verifyScreenshot}
        loading={loading}
        setLoading={setLoading}
      />

      {/* ---------- FOOTER ---------- */}
      <p className="mb-5 text-xs text-gray-500">
        Powered by <span className="font-semibold text-primary">LoyaltyPro</span>
      </p>
    </div>
  );
};

export default ClaimPage;
