"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent } from "@/components/ui/card";
import { Upload, X } from "lucide-react";
import { observer } from "mobx-react-lite";
import { Spinner } from "@/components/ui/spinner";
import { resizeImage } from "@/lib/aiParser";


const ClaimCard = ({ verify, loading, setLoading }) => {
    const [file, setFile] = useState(null);
    const [phone, setPhone] = useState("");
    const [preview, setPreview] = useState(null);

    const handleFileChange = (e) => {
        const selected = e.target.files?.[0];
        if (!selected) return;
        setFile(selected);
        setPreview(URL.createObjectURL(selected));
    };

    const removeImage = () => {
        setFile(null);
        setPreview(null);
    };

    // VERIFY (DeepSeek ONLY)
    const handleVerify = async (e) => {
        e.preventDefault();
        if (!file || phone.length < 10) return;

        setLoading(true);

        const resized = await resizeImage(file);

        // ðŸš€ Send ONLY image + phone to server
        await verify(resized, phone);
    };

    return (
        <Card className="w-full shadow-md border rounded-xl overflow-hidden max-w-sm bg-white">
            <div className="bg-linear-to-r from-primary to-secondary text-white text-center py-4">
                <h1 className="text-lg font-semibold">Claim Your Stamp</h1>
                <p className="text-white/80 text-xs">
                    Upload your UPI payment screenshot
                </p>
            </div>

            <CardContent className="flex flex-col gap-5 py-5">
                <div
                    onClick={!preview ? () => document.getElementById("file")?.click() : undefined}
                    className="relative flex flex-col items-center justify-center border-2 border-dashed border-gray-300 rounded-lg w-full cursor-pointer bg-gray-50 hover:bg-gray-100 p-5 min-h-[170px]"
                >
                    {!preview ? (
                        <div className="flex flex-col items-center gap-2 text-gray-500">
                            <Upload className="w-10 h-10 opacity-70" />
                            <p className="text-sm font-semibold">Tap to upload screenshot</p>
                        </div>
                    ) : (
                        <div className="relative rounded-lg overflow-hidden shadow border">
                            <img src={preview} alt="preview" />

                            {!loading && (
                                <button
                                    onClick={removeImage}
                                    className="absolute top-2 right-2 p-1.5 rounded-full bg-black/50 text-white"
                                >
                                    <X className="w-4 h-4" />
                                </button>
                            )}

                            {loading && (
                                <div className="absolute inset-0 bg-black/30 flex items-center justify-center">
                                    <Spinner />
                                </div>
                            )}
                        </div>
                    )}
                </div>

                <input
                    id="file"
                    type="file"
                    accept="image/*"
                    onChange={handleFileChange}
                    className="hidden"
                />

                <div>
                    <label className="block text-sm font-medium mb-1">Phone</label>
                    <div className="relative">
                        <span className="absolute left-2 top-1/2 -translate-y-1/2 text-gray-500">
                            +91
                        </span>
                        <Input
                            maxLength={10}
                            value={phone}
                            onChange={(e) => setPhone(e.target.value)}
                            className="pl-10"
                        />
                    </div>
                </div>

                <Button
                    disabled={!file || loading || phone.length < 10}
                    className="w-full py-3"
                    onClick={handleVerify}
                >
                    {loading ? <><Spinner /> Verifyingâ€¦</> : "Submit"}
                </Button>
            </CardContent>
        </Card>
    );
};

export default observer(ClaimCard);
